
.selectlists-box-bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(50, 50, 50, 0.5);
  z-index: 9999;
}
.selectlists-box-lists {
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #fff;
  width: 100%;
  overflow-y: auto;
  height: 20rem;
}
.selectlists-box-lists li {
    line-height: 4rem;
    padding: 0 2%;
    font-size: 1.6rem;
    color: #999;
}
.selectlists-box-lists li:not(:first-child) {
    border-top: 1px solid #f8f8f8;
}
.select-first {
  background-color: #f7f7f7;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: absolute;
  bottom: 20rem;
  left: 0;
  width: 100%;
  line-height: 4rem;
  font-size: 1.6rem;
  padding: 0 2%;
}
.selecton {
  color: orange !important;
}



